import { Env } from '@stencil/core';
import ky, { SearchParamsOption } from 'ky';
import type { KyInstance } from 'ky/distribution/types/ky';
import { ApiKeySchema, baseUrlSchema, querySchema } from './schemas';
import { BaseUrl, KeyData, OrderDto, QueryDto } from './types';
import { TGetBrandsResponse } from '../types/brands.types';
import { TGetAllCategoriesResponse } from '../types/categories.types';
import { TGetAllCountriesResponse } from '../types/countries.types';
import { TGetOrdersResponse, TGetOrderResponse, TCreateOrderResponse } from '../types/orders.types';
import { TGetProductsResponse, TGetProductResponse } from '../types/products.types';

export interface API {
  api: KyInstance | null;
  key: string;
  init(key: string): Promise<void>;
  getCategories(query?: QueryDto): Promise<any>;
  getBrands(query?: QueryDto): Promise<any>;
  getProducts(query?: QueryDto): Promise<any>;
  getProduct(id: string): Promise<any>;
  getOrder(orderNumber: string): Promise<any>;
  getOrders(query?: QueryDto): Promise<any>;
  createOrder(order: OrderDto): Promise<any>;
  getKey(key: string): Promise<KeyData>;
  setBaseUrl(url: string): void;
}

export class CharmaAPI implements API {
  api: KyInstance | null;
  key: string;
  keyMetadata: KeyData;
  private baseUrl = `${Env.API_BASE_URL}`;

  async init(key: string) {
    const keyData = ApiKeySchema.safeParse(key);
    if (!keyData.success) throw new Error('Key is invalid');

    this.key = keyData.data;
    this.api = ky.create({
      prefixUrl: this.baseUrl,
      headers: { Authorization: `Bearer ${this.key}` },
      timeout: 6000,
    });

    const invalidKeyEvent = new CustomEvent('invalid-charma-key', {
      bubbles: true,
    });

    return await this.getKey()
      .then(res => {
        if (!res.active) {
          this.api = null;
          document.body.dispatchEvent(invalidKeyEvent);
          throw new Error('Charma API Key is invalid!');
        }
        return this.getCountries().then(data => {
          this.keyMetadata = { ...res, countries: data };
        });
      })
      .catch(_err => {
        this.api = null;
        document.body.dispatchEvent(invalidKeyEvent);
        throw new Error('Charma API Key is invalid!');
      });
  }

  setBaseUrl(baseUrl: BaseUrl) {
    if (baseUrlSchema.safeParse(baseUrl).success) {
      this.baseUrl = baseUrl;
    } else {
      throw new Error('baseUrl is not valid URL!');
    }
  }

  async getCategories(query?: QueryDto): Promise<TGetAllCategoriesResponse> {
    try {
      const parsedQuery = querySchema.parse(query);
      const res = await this.api.get('categories', {
        searchParams: parsedQuery as SearchParamsOption,
      });
      return await res.json();
    } catch (error) {
      if (error.name === 'HTTPError') {
        return await error.response.json();
      }
    }
  }

  async getCountries() {
    try {
      const res = await this.api.get('countries');
      const resJson: TGetAllCountriesResponse = await res.json();
      return resJson.countries;
    } catch (error) {
      if (error.name === 'HTTPError') {
        return await error.response.json();
      }
    }
  }

  async getBrands(query?: QueryDto): Promise<TGetBrandsResponse> {
    try {
      const parsedQuery = querySchema.parse(query);
      const res = await this.api.get('brands', {
        searchParams: parsedQuery as SearchParamsOption,
      });
      return await res.json();
    } catch (error) {
      if (error.name === 'HTTPError') {
        return await error.response.json();
      }
    }
  }

  async getProducts(query?: QueryDto): Promise<TGetProductsResponse> {
    try {
      const parsedQuery = querySchema.parse(query);
      const res = await this.api.get('products', {
        searchParams: parsedQuery as SearchParamsOption,
      });
      return await res.json();
    } catch (error) {
      if (error.name === 'HTTPError') {
        return await error.response.json();
      }
    }
  }

  async getProduct(id: string): Promise<TGetProductResponse> {
    if (!id) throw new Error('Product ID is required');
    try {
      const res = await this.api.get(`products/${id}`);
      return await res.json();
    } catch (error) {
      if (error.name === 'HTTPError') {
        return await error.response.json();
      }
    }
  }

  async getOrder(orderNumber: string): Promise<TGetOrdersResponse> {
    if (!orderNumber) throw new Error('Order Number is required');
    try {
      const res = this.api.get(`orders/${orderNumber}`);
      return await res.json();
    } catch (error) {
      if (error.name === 'HTTPError') {
        return await error.response.json();
      }
    }
  }

  async getOrders(query?: QueryDto): Promise<TGetOrderResponse> {
    try {
      const parsedQuery = querySchema.parse(query);
      const res = this.api.get(`orders`, {
        searchParams: parsedQuery as SearchParamsOption,
      });
      return await res.json();
    } catch (error) {
      if (error.name === 'HTTPError') {
        return await error.response.json();
      }
    }
  }

  async createOrder(order: OrderDto): Promise<TCreateOrderResponse> {
    try {
      const res = await this.api.post(`order`, {
        json: order,
      });
      if (!res.ok) {
        throw new Error('Something went wrong');
      }
      return await res.json();
    } catch (error) {
      if (error.name === 'HTTPError') {
        return await error.response.json();
      }
      return await error;
    }
  }

  async getKey(): Promise<KeyData> {
    try {
      const res = await this.api.get(`get-key/${this.key}`);
      return await res.json();
    } catch (error) {
      if (error.name === 'HTTPError') {
        return await error.response.json();
      }
    }
  }
}
